.container-slider {
  /* max-width: 90rem; */
  max-width: 1600px;

  /* width: 82%; */
  /* height: auto; */
  height: 37rem;
  /* margin: 100px auto 0; */
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 800px) {
  .SlideArrow {
    width: 2rem;
    height: 2rem;
  }

  .btn-slide,
  .prev,
  .next {
    width: 2.2rem;
    height: 2.2rem;
  }
}

.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  /* width: 100rem; */
  height: 100%;
  object-fit: cover;
}
.active-anim {
  opacity: 1;
}

.btn-slide {
  width: 3.4rem;
  height: 3.4rem;
  border-radius: 50%;
  /* background: #f1f1f1; */
  background: transparent;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  border: 0px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .btn-slide,
  .prev,
  .next {
    width: 0.2rem;
    height: 0.2rem;
  }
}
.btn-slide :hover {
  color: #ffdd02;
  transition: all 0.2s linear;
}

.prev {
  color: #fff;
  top: 50%;
  left: 20px;
  transform: translateY(-60%);
}
.next {
  color: #fff;
  top: 50%;
  right: 20px;
  transform: translateY(-60%);
}

.container-dots {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}
.dot {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  border: 0.4px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
}
.dot:hover {
  cursor: pointer;
}
.dot.active {
  background: rgb(32, 32, 32);
}
.autoSlide-button {
  background: #f1f1f1;
  color: #202020;
  font-weight: 800;
  position: absolute;
  display: flex;
  bottom: 10px;
  left: 10px;
  padding: 10px;
  border: none;
  border-radius: 10px;
  opacity: 0.35;
  cursor: pointer;
}

.autoSlide-button:hover {
  opacity: 1;
}
.container {
  position: relative;
  text-align: center;
  color: rgb(235, 233, 233);
  height: 42rem;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* //Banner 1 */
.banner1,
.banner2,
.banner3 {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 100vw;
  height: 42rem;
  transform: translate(-50%, -50%);
  display: inline-block;
  /* background: rgba(0, 0, 0, 0.5); */
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.89));

  /* padding: 2rem 1rem; */
  text-align: center;
  text-transform: capitalize;
}
.banner1 h1,
.banner2 h1,
.banner3 h1 {
  color: #ffffff;
  font-size: 2rem;
  position: absolute;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  top: 36.5rem;
  left: 15vw;
  font-weight: 900;
}
.banner1 div,
.banner2 div,
.banner3 div {
  width: 10rem;
  height: 5px;
  background: white;
  margin: 1.7rem auto;
}
.banner1 p,
.banner2 p,
.banner3 p {
  /* font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #ffffff; */
  font-size: 0.8rem;
  position: absolute;
  display: flex;
  left: 15vw;
  top: 40.5rem;
}
.banner1 h4 {
  font-size: 1rem;
  letter-spacing: 0.05em;
  /* font-family: "ABEEZEE", sans-serif; */
  /* font-family: "Belleza", sans-serif; */
  margin-bottom: 2rem;
}

@media screen and (max-width: 575px) {
  .banner1,
  .banner2,
  .banner3 {
    width: 100%;
    height: 50rem;
    padding: 2rem 1rem;
  }
  .banner1 div {
    margin: 3.7rem auto;
  }
}
@media screen and (min-width: 576px) {
  .banner1,
  .banner2,
  .banner3 {
    padding: 2rem 3rem;
  }
  .banner1 h1,
  .banner2 h1,
  .banner3 h1 {
    font-size: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .banner1,
  .banner2,
  .banner3 {
    padding: 2rem 6rem;
  }
  .banner1 h1,
  .banner2 h1,
  .banner3 h1 {
    font-size: 3rem;
    position: absolute;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    display: flex;
    top: 29rem;
    font-weight: 900;
    left: 5.05vw;
  }
  .banner1 p,
  .banner2 p,
  .banner3 p {
    font-size: 1.1rem;
    position: absolute;
    display: flex;
    top: 34rem;
    left: 5.05vw;
  }
}

